import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    benefits_view_status: false,
    isshowRegform: false
  },
  getters: {},
  mutations: {
    dontShowPo(state, params) {
      state.benefits_view_status = params;
    },
    showRegform(state, params) {
      state.isshowRegform = params;
    }
  },
  actions: {},
  modules: {}
});
